.contact{
    background-color: #fff;
}

.contact-header{
    position: relative;
}
.contact-image img{
    height: 70vh;
    width: 100%;
    object-fit: cover;
}
.contact-title{
    position: absolute;
    bottom: 10rem;
    left: 10%;
    z-index: 99;
}
.contact-title h1{
    font-size: 82px;
    font-weight: bold;
    color: white;
}
.contact-content{
    margin: 6rem;
}
.contact-content h1{
    font-size: 43px;
    font-weight: 400;
}
.contact-content p{
    max-width: 800px;
    font-size: 19px;
    line-height: 32px;
    color: #5A5A5A;
}
.contact-opacity{
    position: absolute;
    background-color: rgba(0, 0, 0, 0.7);
    width: 100%;
    height: 70vh;
    z-index: 4;
}
.contact-vertical{
    content: "";
    width: 80px;
    height: 5px;
    background-color: #246a47;
    margin: 1rem 0;
}
.contact-address{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 6rem 6rem 0;

}
.contact-form{
    flex: 1;
    height: 800px;
    padding: 2rem 3rem;
    background-color: #f1f1f1;
}
.contact-form h1{
    margin-bottom: 4rem;
    font-size: 33px;
    font-weight: 500;
}

.contact-form input{
    width: 97%;
    height: 70px;
    margin-bottom: 2rem;
    font-size: 19px;
}
.contact-form button{
    background-color: #246a47;
    color: white;
    padding: 1rem 3rem;
    margin: 0;
    transition: all 0.5s;
    cursor: pointer;
}
.contact-form button:hover{
    background-color: white;
    border: 1px solid #246a47;
    color: #246a47;
    padding: 1rem 3rem;
    margin: 0;
}
.contact-form textarea{
    width: 95%;
    height: 200px;
    margin-bottom: 2rem;
    font-size: 19px;
    border: none;
    padding: 1rem;
}
.contact-details{
    flex: 1;
    height: 800px;
    background-color: rgb(248, 249, 250);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    padding: 2rem 3rem;
}
.contact-details h1{
    margin-bottom: 4rem;
    font-size: 33px;
    font-weight: 500;
}
.contact-info{
    display: grid;
    grid-template-columns: 280px 280px;
   gap: 30px;
}
.CD{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
}
.CD-text h3{
    font-size: 23px;
    margin: 0;
}
.CD-image{
    background-color: rgb(221, 255, 244);
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100%; 
    margin-right: 1rem;
}
.CD-image img{
    color: #246a47;
}
.CD-text p{
    font-size: 23px;
    font-weight: 100;
}
.contact-SM{
    margin-top: 4rem;
}
.contact-SM h1{
    font-size: 33px;
    font-weight: 500;
    margin:0;
}
.SM-icons{
    margin-top: 2.5rem;
}
.sm-logos{
    font-size: 23px;
    margin-right: 1rem;
    background-color: rgb(221, 255, 244);
    padding: 1rem;
    border-radius: 100%;
    color: #246a47;
}